/* eslint-disable camelcase */
import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Row,
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import StateButton from '../../../components/StateButton';
import { setProfileData,getProfileData } from '../../../helpers/Utils';
import { updateProfileData } from '../../../redux/actions';
import instance from '../../../helpers/axiosInstance';

const About = ({ match, currentUser }) => {
  const { email } = currentUser;
  const profileData = getProfileData();
  const {  user_id,id,instagram,organization_name,representative_name,linkedin,  contact_no,address, bio,websiteUrl,country } = profileData;
  console.log(id);
  const [editAbout, setEditAbout] = useState(false);

  const [formValues, setFormValues] = useState({
    organization_name,
    representative_name,
    instagram,
    linkedin,
    contact_no,
    address,
    bio,
    websiteUrl,
    country,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmitAbout = async (newData) => {
    const response = await instance({
      method: 'POST',
      url: '/updateProfile',
      data: { user_id,id, ...newData },
    });

    const { data } = response;

    if (data.success) {
      setProfileData({ ...profileData, ...newData });
      return new Promise((success) => {
        success('Profile updated successfully!');
      });
    }
    return new Promise(() => {
      console.log("failed!");
    });
  };


  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.about" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setEditAbout(!editAbout);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>
            <CardBody>
              <CardTitle>
                <h2 className="lead">
                  <IntlMessages id="user.update-about" />
                </h2>
              </CardTitle>

              <Separator className="mb-3" />

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Name | College | Institute | Company" />
                </Label>
                <Input
                  type="text"
                  name="Organization Name | College | Institute | Company"
                  value={formValues.organization_name}
                  onChange={(event) => {
                    handleChange(event, 'organization_name');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Email" />
                </Label>
                <Input type="text" name="email" value={email} disabled />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Representative Name" />
                </Label>
                <Input
                  type="text"
                  name="Representative_Name"
                  value={formValues.representative_name}
                  onChange={(event) => {
                    handleChange(event, 'representative_name');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

           

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Country" />
                </Label>
                <Input type="text" name="country" value={formValues.country} 
                  onChange={(event) => {
                    handleChange(event, 'country');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Address" />
                </Label>
                <Input
                  type="text"
                  name="address"
                  value={formValues.address}
                  onChange={(event) => {
                    handleChange(event, 'address');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

{/* 
              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.phone" />
                </Label>
                <Input
                  type="text"
                  name="phone"
                  value={formValues.contact_no}
                  onChange={(event) => {
                    handleChange(event, 'contact_no');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.gender" />
                </Label>
                {editAbout ? (
                  <Select
                    components={{ Input: CustomSelectInput }}
                    className="react-select"
                    classNamePrefix="react-select"
                    name="select-gender"
                    defaultInputValue={formValues.gender}
                    onChange={(selectedOption) => {
                      setFormValues({
                        ...formValues,
                        gender: selectedOption.value,
                      });
                    }}
                    options={genderOptions}
                  />
                ) : (
                  <Input
                    type="text"
                    name="gender"
                    value={formValues.gender}
                    disabled={!editAbout}
                  />
                )}
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.t-shirt-size" />
                </Label>
                {editAbout ? (
                  <Select
                    components={{ Input: CustomSelectInput }}
                    className="react-select"
                    classNamePrefix="react-select"
                    name="select-t-shirt-size"
                    defaultInputValue={formValues.tshirt_size}
                    onChange={(selectedOption) => {
                      setFormValues({
                        ...formValues,
                        tshirt_size: selectedOption.value,
                      });
                    }}
                    options={shirtSizeOptions}
                  />
                ) : (
                  <Input
                    type="text"
                    name="t-shirt-size"
                    value={formValues.tshirt_size}
                    disabled={!editAbout}
                  />
                )}
              </FormGroup> */}

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Bio" />
                </Label>
                <Input
                  type="textarea"
                  name="bio"
                  rows="6"
                  placeholder="About Yourself"
                  value={formValues.bio}
                  onChange={(event) => {
                    handleChange(event, 'bio');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="Organization Social Profile" />
                </Label>
                <Input
                  type="text"
                  name="linkedin"
                  placeholder="Linkedin Profile"
                  value={formValues.linkedin}
                  onChange={(event) => {
                    handleChange(event, 'linkedin');
                  }}
                  disabled={!editAbout}
                /><br />
                <Input
                  type="text"
                  name="Instagram"
                  placeholder="Instagram Profile"
                  value={formValues.instagram}
                  onChange={(event) => {
                    handleChange(event, 'instagram');
                  }}
                  disabled={!editAbout}
                /><br />
                <Input
                  type="text"
                  name="website"
                  placeholder="Website URL"
                  value={formValues.websiteUrl}
                  onChange={(event) => {
                    handleChange(event, 'websiteUrl');
                  }}
                  disabled={!editAbout}
                />
              </FormGroup>


              {editAbout && (
                <FormGroup className="text-center">
                  <StateButton
                    id="submitAndNextButton"
                    color="primary"
                    onClick={() => handleSubmitAbout(formValues)}
                    next={() => {
                      setEditAbout(false);
                    }}
                  >
                    <IntlMessages id="forms.submit" />
                  </StateButton>
                </FormGroup>
              )}
            </CardBody>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ history, authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData, history };
};

const mapActionsToProps = {
  updateProfileAction: updateProfileData,

};


export default connect(mapStateToProps, mapActionsToProps)(About);
